/* eslint-disable eqeqeq */
import React, { useState, useEffect, useContext } from 'react';
import { Modal } from 'react-bootstrap';
import styled, { css, ThemeContext } from 'styled-components';
import FacebookLogin from 'react-facebook-login';
import { GLogin } from './GoogleLogin';
import './MyCenteredModal.css';
import { Spinner, Container, Row, Col, Alert, Carousel } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { ThemeProvider } from 'styled-components';
import * as api from '../Services/api';
// style facebook button must be made with css
import '../styles/facebook-button.css';

// styled
import { StyledButton } from '../styles/Styles';
import { Dropdown } from 'react-bootstrap';
import { useSelector } from 'react-redux';

export const StreamDialog = (props) => {
	const theme = useContext(ThemeContext);
	const [title, setTitle] = useState('Iniciando Stream');
	const [googlePermission, setGooglePermission] = useState(true);

	const [indexModal, setIndexModal] = useState(0);

	const handleIndexModal = (selectedIntex) => {
		setIndexModal(selectedIntex);
	};

	// settings transmition
	const [titleTransmission, setTitleTransmission] = useState('');
	const [subtitleTransmission, setSubtitleTransmission] = useState('');
	const [imageTransmission, setImageTransmission] = useState('');
	const [variantAlert, setVariantAlert] = useState('');
	const [msgAlert, setMsgAlert] = useState('');
	const [showAlert, setShowAlert] = useState(false);
	const [images, setImages] = useState([]);

	const user = useSelector((state) => state.user);

	useEffect(() => {
		loadImages();
	}, []);

	const loadImages = async () => {
		const params = {
			id: user.profile.id,
			folder: 'images',
		};

		try {
			const response = await api.getFiles(params);
			setImages(response);
		} catch (error) {
			console.warn('loadImages ->', error);
		}
	};

	const startTransmission = () => {
		if (!verify()) {
			return dispatchAlert(
				true,
				'Erro: Informe os dados para iniciar uma nova transmissão.'
			);
		}
		const data = {
			title: titleTransmission,
			subtitle: subtitleTransmission,
			primaryCamera: props.mainCamera,
			image: imageTransmission,
		};
		console.log(data);
		props.startTransmission(data);
		props.onHide();
	};

	const verify = () => {
		if (!titleTransmission) return false;
		if (!subtitleTransmission) return false;
		return true;
	};

	const closeAlert = () => {
		setShowAlert(false);
	};

	const dispatchAlert = (error, message) => {
		setVariantAlert(error ? 'danger' : 'success');
		setMsgAlert(message);
		setShowAlert(true);
	};

	return (
		<ThemeProvider theme={theme}>
			<Modal
				scrollable
				{...props}
				aria-labelledby='contained-modal-title-vcenter'
				centered>
				<Modal.Body>
					<div className='closebox-modal'>
						<FontAwesomeIcon icon={faTimes} onClick={(e) => props.onHide()} />
					</div>
					<Container style={{ minHeight: 600 }}>
						<Carousel
							style={{ minHeight: '100%' }}
							controls={false}
							indicators={false}
							activeIndex={indexModal}>
							<Carousel.Item>
								<div className='addUserForm' autocomplete='off'>
									<Row>
										<Col xl={12} className='header-modal'>
											<Title>{title}</Title>
											{/* {loading ? <Spinner animation="border" /> : null} */}
										</Col>
										<Col className='col3' xl={12}>
											<div className='grouping'>
												<label className='labels-contrato' htmlfor='nome'>
													Título da Transmissão
												</label>
												<input
													type='text'
													name='title'
													autoComplete='false'
													className='inputs-addUser'
													placeholder='título'
													onChange={(e) => setTitleTransmission(e.target.value)}
													value={titleTransmission}
													required></input>
											</div>
										</Col>
										<Col className='col3' xl={12}>
											<div className='grouping'>
												<label className='labels-contrato' htmlfor='nome'>
													Descrição
												</label>
												<input
													type='text'
													name='description'
													autoComplete='false'
													className='inputs-addUser'
													placeholder='descrição'
													onChange={(e) =>
														setSubtitleTransmission(e.target.value)
													}
													value={subtitleTransmission}
													required></input>
											</div>
										</Col>
										<Col className='col3' xl={12}>
											<StyledButton
												style={{ margin: 0, width: 344 }}
												onClick={() => handleIndexModal(1)}>
												Imagem para legendas
											</StyledButton>
										</Col>
										<Col className='col3' xl={12}>
											<GLogin
												setToken={props.setGoogleAccessToken}
												setGooglePermission={setGooglePermission}
											/>
										</Col>
										<Col className='col3' xl={12}>
											<FacebookLogin
												appId='1068601560611480'
												autoLoad={false}
												fields='name,email,picture'
												scope='public_profile, publish_video'
												callback={props.responsefacebook}
												icon={<FaceboockIcon />}
												cssClass='facebook-button'
												textButton='Transmitir com Facebook'
											/>
										</Col>
										<Col className='col3' xl={12}>
											<Alert
												dismissible
												onClose={closeAlert}
												show={showAlert}
												variant={variantAlert}>
												{msgAlert}
											</Alert>
										</Col>
										<Col className='col3' xl={12}>
											<button
												disabled={!titleTransmission || !subtitleTransmission}
												style={{ marginBottom: 32 }}
												className='login-button'
												type='submit'
												onClick={startTransmission}>
												Transmitir
											</button>
										</Col>
									</Row>
								</div>
							</Carousel.Item>
							<Carousel.Item>
								<div
									style={{
										display: 'flex',
										flexDirection: 'column',
									}}>
									<div
										style={{
											display: 'flex',
											flexDirection: 'column',
											alignItems: 'center',
										}}>
										{!!images.length && (
											<>
												{images.map((ele) => (
													<ImagePicker
														selected={imageTransmission === ele}
														onSelect={() => {
															setImageTransmission(ele);
															setTimeout(() => setIndexModal(0), 300);
														}}
														value={ele}
													/>
												))}
												<div
													style={{
														maxWidth: '80%',
														padding: 24,
													}}>
													<Description>
														Selecione a imagem a ser exibida junto à legenda.
														Caso nenhuma seja selecionada, uma imagem padrão
														será exibida.
													</Description>
												</div>
											</>
										)}
										{!images.length && (
											<>
												<Text>Nenhuma imagem encontrada.</Text>
												<Description>
													Cadastre uma nova imagem na guia Arquivos.
												</Description>
											</>
										)}
									</div>
								</div>
								<Col className='col3' xl={12}>
									<StyledButton
										variant='danger'
										style={{ margin: 0, marginBottom: 16 }}
										onClick={() => {
											handleIndexModal(0);
											setImageTransmission('');
										}}>
										Cancelar e voltar
									</StyledButton>
								</Col>
							</Carousel.Item>
						</Carousel>
					</Container>
				</Modal.Body>
			</Modal>
		</ThemeProvider>
	);
};

// temporary facebook logo
const FaceboockIcon = () => (
	<svg
		xmlns='http://www.w3.org/2000/svg'
		xmlnsXlink='http://www.w3.org/1999/xlink'
		style={{ width: 20, height: 20, marginRight: 8 }}
		role='img'
		preserveAspectRatio='xMidYMid meet'
		viewBox='0 0 750 850'>
		<path
			d='M659 8q34 0 59 24t24 58v577q0 35-24 59t-59 24H494V461h83l23-102H494v-69q0-22 13-41t44-19h49V123h-74q-79 0-117 42t-38 110v84h-83v102h83v289H82q-34 0-58-24T0 667V90q0-34 24-58T82 8h577z'
			fill='currentColor'></path>
	</svg>
);

//
const ImagePicker = ({ selected, onSelect, value }) => {
	return (
		<Picker selected={selected} onClick={onSelect}>
			<img src={value} alt='img' />
		</Picker>
	);
};

const Picker = styled.div`
	margin: 8px;
	color: #333;
	cursor: pointer;
	border-radius: 5px;
	background: #cacaca;
	border: solid 2px;
	border-color: transparent;
	width: 200px;
	height: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all ease 0.3s;
	overflow: hidden;

	${({ selected }) =>
		selected
			? css`
					border-color: #333;
					color: #fff;
			  `
			: null};

	img {
		min-width: 100%;
		height: auto;
	}
`;

const Title = styled.h1`
	font: normal normal 600 18px/28px Poppins;
	color: ${({ theme }) => theme.colors.Text1};
	text-transform: none;
`;

const Text = styled.span`
	font: normal normal 600 16px/25px Poppins;
	color: ${({ theme }) => theme.colors.Text1};
	text-transform: none;
`;

const Description = styled.span`
	font: normal normal 400 14px/20px Poppins;
	color: ${({ theme }) => theme.colors.Text1};
	text-transform: none;
`;
